<template>
  <div class="wsy_box">
    <!-- 用户table区域 -->
    <el-card class="box-card">
      <div class="el-card_body">
        <!-- 顶部搜索区域 -->
        <div class="wsy_top_search">
          <div class="top_search_left">
            <el-input
              placeholder="请输入内容"
              v-model="getUserListParams.keyword"
              @clear="clear()"
              clearable
            ></el-input>
            <el-button icon="el-icon-search" @click="findUser()"></el-button>
          </div>
          <div class="top_search_right">
            <el-button type="primary" @click="addUserMotal = true">添加用户</el-button>
          </div>
        </div>
        <!-- 表格区域 -->
        <el-table :data="userTable" stripe border style="width: 100%">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column prop="username" label="姓名"></el-table-column>
          <el-table-column prop="email" label="邮箱"></el-table-column>
          <el-table-column prop="mobile" label="电话"></el-table-column>
          <el-table-column prop="role_name" label="角色"></el-table-column>
          <el-table-column prop="mg_state" label="状态">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
                @change="editState(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <div>
                <el-tooltip :enterable="false" content="编辑" placement="top">
                  <el-button
                    size="small"
                    type="primary"
                    icon="el-icon-edit"
                    @click="editUserMotai(scope.row)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip :enterable="false" content="删除" placement="top">
                  <el-button
                    size="small"
                    type="danger"
                    icon="el-icon-delete"
                    @click="removeUserMotal(scope.row.id)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip :enterable="false" content="设置" placement="top">
                  <el-button
                    size="small"
                    type="warning"
                    icon="el-icon-setting"
                    @click="settingUserRoleMotai(scope.row)"
                  ></el-button>
                </el-tooltip>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- <table class="wsy_table" border="1" cellspacing="0" cellpadding="0">
          <thead>
            <tr>
              <td>#</td>
              <td>姓名</td>
              <td>邮箱</td>
              <td>电话</td>
              <td>角色</td>
              <td>状态</td>
              <td>操作</td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in userTable" :key="index">
              <td>{{ item.id }}</td>
              <td>{{ item.username }}</td>
              <td width="180">{{ item.email }}</td>
              <td width="180">{{ item.mobile }}</td>
              <td>{{ item.role_name }}</td>
              <td>
                <el-switch
                  v-model="item.mg_state"
                  active-color="#409EFF"
                  inactive-color="#DCDFE6"
                  @change="editUserState(item.id,item.mg_state)"
                ></el-switch>
              </td>
              <td width="200">
                <el-button
                  size="small"
                  type="primary"
                  icon="el-icon-edit"
                  @click="editUserMotai(item)"
                ></el-button>
                <el-button
                  size="small"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeUserMotal(item.id)"
                ></el-button>
                <el-button
                  size="small"
                  type="warning"
                  icon="el-icon-setting"
                  @click="settingUserRoleMotai(item)"
                ></el-button>
              </td>
            </tr>
          </tbody>
        </table>-->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="getUserListParams.pageNum"
          :page-sizes="[1,2,5]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalNum"
        ></el-pagination>
      </div>
    </el-card>

    <!-- 提交用户模态框 -->
    <el-dialog title="添加用户" :visible.sync="addUserMotal">
      <el-form
        :model="addUserformData"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addUserformData.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addUserformData.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="addUserformData.email"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="addUserformData.mobile"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addUserMotal = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改用户信息模态框 -->
    <el-dialog title="修改用户" :visible.sync="editUserMotal">
      <el-form
        :model="editUserformData"
        :rules="editrules"
        ref="editForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="name">
          <el-input v-model="editUserformData.username" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editUserformData.email"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="editUserformData.mobile"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editUserMotal = false">取 消</el-button>
        <el-button type="primary" @click="editUser()">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分配用户角色模态框 -->
    <el-dialog title="分配角色" :visible.sync="settingUserMotal">
      <el-form
        :model="settingUserformData"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm settingUser"
      >
        <p>当前的用户：{{ settingUserformData.username }}</p>
        <p>当前的角色：{{ settingUserformData.role_name }}</p>
        <el-form-item label="分配新角色">
          <el-select v-model="settingSelect" placeholder="请选择">
            <el-option label="角色一" value="1"></el-option>
            <el-option label="角色二" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="settingUserMotal = false">取 消</el-button>
        <el-button type="primary" @click="settingUser()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    // 用户名验证
    // var name = (rule,value,callback)=>{
    //   if(!value){
    //     return callback(new Error("请输入用户名"))
    //   }else if(value.length < 2){
    //     return callback(new Error("用户名不能少于3个字，不能大于15个字"))
    //   }else{
    //     callback()
    //   }
    // }
    //   邮箱验证
    let email = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("邮箱地址不能为空"));
      }
      let reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/;
      if (!reg.test(value)) {
        callback(new Error("请输入正确的邮箱地址"));
      } else {
        callback();
      }
    };
    // 手机号验证
    let phone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号码不能为空"));
      }
      let reg = /^[1]([3-9])[0-9]{9}$/;
      if (!reg.test(value)) {
        callback(new Error("请输入正确的手机号码"));
      } else {
        callback();
      }
    };
    return {
      // 获取列表数据参数
      getUserListParams: {
        keyword: "",
        pagenum: 1,
        pagesize: 1
      },
      totalNum: 0,
      userTable: [],
      addUserformData: {
        // 添加用户表单数据
        username: "www",
        password: "123456",
        email: "2793942713@qq.com",
        mobile: "15076978269"
      },
      editUserformData: {
        // 修改用户表单数据
        username: "www",
        id: "",
        email: "2793942713@qq.com",
        mobile: "15076978269"
      },
      // 分配用户角色表单数据
      settingSelect: "",
      settingUserformData: {
        id: "",
        rid: ""
      },
      //   要删除的用户ID
      removeUserID: 0,
      //   添加用户模态框状态
      addUserMotal: false,
      //   修改模态框状态
      editUserMotal: false,
      // 分配用户角色模态框状态
      settingUserMotal: false,
      formLabelWidth: "120px",
      //   用户提交模态框表单验证
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, max: 15, message: "长度在 6 到 15 个字符", trigger: "blur" }
        ],
        email: [{ required: true, validator: email, trigger: "blur" }],
        mobile: [{ required: true, validator: phone, trigger: "blur" }]
      },
      // 修改用户数据模态框表单验证
      editrules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 10 个字符", trigger: "blur" }
        ],
        email: [{ required: true, validator: email, trigger: "blur" }],
        mobile: [{ required: true, validator: phone, trigger: "blur" }]
      },
      ru: {}
    };
  },
  created() {},
  mounted() {
    this.getUserList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取用户数据
    async getUserList() {
      let { data } = await this.$axios.get(
        `/users?query=${this.getUserListParams.keyword}&pagenum=${this.getUserListParams.pagenum}&pagesize=${this.getUserListParams.pagesize}`
      );
      // console.log(data.data);

      this.totalNum = data.data.total;
      this.getUserListParams.pagenum = data.data.pagenum;
      this.userTable = data.data.users;
    },
    handleSizeChange(val) {
      this.getUserListParams.pagesize = val;
      this.getUserList();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.getUserListParams.pagenum = val;
      this.getUserList();
      // console.log(`当前页: ${val}`);
    },
    // 添加用户
    async addUser() {
      let { data } = await this.$axios.post("/users", this.addUserformData);
      // console.log(data);
      if (data.meta.status == 201) {
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
      } else {
        this.$message({
          message: data.meta.msg,
          type: "error"
        });
      }
      this.addUserformData.username = "www";
      this.addUserformData.password = "123456";
      this.addUserformData.email = "2793942713@qq.com";
      this.addUserformData.mobile = "15076987456";
      this.getUserList();
      this.addUserMotal = false;
    },
    // 点击修改按钮，显示修改模态框
    editUserMotai(editInfo) {
      this.editUserformData = editInfo;
      this.editUserMotal = true;
    },
    // 修改用户信息
    async editUser() {
      let { data } = await this.$axios.put(
        `users/${this.editUserformData.id}`,
        {
          email: this.editUserformData.email,
          mobile: this.editUserformData.mobile
        }
      );
      if (data.meta.status == 200) {
        this.editUserMotal = false;
        this.$message({
          message: data.meta.msg,
          type: "success"
        });
      } else {
        this.$message({
          message: data.meta.msg,
          type: "error"
        });
      }
      this.getUserList();
    },
    // 点击删除，删除用户模态框显示
    removeUserMotal(removeUserID) {
      this.removeUserID = removeUserID;
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$axios.delete(`/users/${removeUserID}`).then(({ data }) => {
            if (data.meta.status == 200) {
              this.$message({
                type: "success",
                message: data.meta.msg
              });
            } else {
              this.$message({
                type: "error",
                message: data.meta.msg
              });
            }
          });
          // let { data } = await this.$axios.delete(`/users/${removeUserID}`)
          //   if (data.meta.status == 200) {
          //     this.$message({
          //       type: "success",
          //       message: data.meta.msg
          //     });
          //   } else {
          //     this.$message({
          //       type: "error",
          //       message: data.meta.msg
          //     });
          //   }
          //    console.log(data);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
      this.getUserList();
    },
    // 根据用户ID查询用户信息
    findUser() {
      this.getUserList();
    },
    // 分配用户角色模态框
    settingUserRoleMotai(item) {
      this.settingUserformData = item;

      this.settingUserMotal = true;
    },
    // 修改用户角色
    settingUser() {
      //
      this.$axios
        .put(`users/${this.settingUserformData.id}/role`, {
          rid: this.settingSelect
        })
        .then(({ data }) => {
          if (data.meta.status == 200) {
            this.$message({
              type: "success",
              message: data.meta.msg
            });
          } else {
            this.$message({
              type: "error",
              message: data.meta.msg
            });
          }
          this.settingSelect = "";
          this.settingUserMotal = false;
        });
    },

    // 修改用户状态  普通表格渲染
    // editUserState(uId, type) {
    //   // console.log(uId,type);
    //   this.$axios.put(`users/${uId}/state/${type}`).then(({ data }) => {
    //     // console.log(data);
    //     if (data.meta.status == 200) {
    //       this.$message({
    //         type: "success",
    //         message: data.meta.msg
    //       });
    //     } else {
    //       this.$message({
    //         type: "error",
    //         message: data.meta.msg
    //       });
    //     }
    //   });
    // },
    // 修改用户状态  Element-ui渲染
    editState(item) {
      this.$axios
        .put(`users/${item.id}/state/${item.mg_state}`)
        .then(({ data }) => {
          if (data.meta.status == 200) {
            this.$message({
              type: "success",
              message: data.meta.msg
            });
          } else {
            this.$message({
              type: "error",
              message: data.meta.msg
            });
          }
        });
    },
    // editMotai(item) {
    //   console.log(item);
    // },
    // 点击输入框中的清空，重新调用获取数据方法
    clear() {
      this.getUserList();
    }
  },
  filters: {},
  computed: {
    // pageSize() {
    //   return this.totalNum / this.pageNum;
    // }
  },
  watch: {}
};
</script>

<style lang='scss' scoped>
.box-card {
  margin-top: 15px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
  border: 1px solid #ebeef5;
  background-color: #fff;
  color: #303133;
  transition: 0.3s;
  border-radius: 4px;
  overflow: hidden;
  > .el-card_body {
    padding: 20px;
  }
}
.wsy_top_search {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  > .top_search_left {
    display: flex;
    width: 40%;
    margin-right: 20px;
  }
}
.wsy_table {
  font-size: 12px;
  margin-top: 20px;
  width: 100%;
  border: 1px #efefef;
  color: #606266;
  tr {
    > td {
      box-sizing: border-box;
      line-height: 40px;
      padding-left: 10px;
    }
  }
}
.el-pagination {
  margin-top: 20px;
}
.el-form-item__label {
  text-align: left;
}
.settingUser > p {
  padding-left: 20px;
}
.el-table {
  margin-top: 20px;
}
</style>
